import React from 'react';
import { Accordion } from 'react-bootstrap';
import EditableField from './EditableField';

function CompanyInfoAccordion({ data, isEditing, onChange }) {
  // Agregar un nuevo item al acorden
  const handleAddItem = () => {
    const newItem = { title: 'Nuevo despliegue', content: 'Descripcin nueva.' };
    onChange([...(Array.isArray(data) ? data : []), newItem]);
  };

  // Eliminar un item del acorden
  const handleRemoveItem = (index) => {
    if (window.confirm('Seguro que deseas eliminar este despliegue?')) {
      const newData = data.filter((_, i) => i !== index);
      onChange(newData);
    }
  };

  const handleItemChange = (index, field, value) => {
    const newData = data.map((item, i) => {
      if (i === index) {
        return { ...item, [field]: value };
      }
      return item;
    });
    onChange(newData);
  };

  if (!Array.isArray(data) || data.length === 0) {
    if (typeof data === 'string') {
      return (
        <EditableField
          isEditing={isEditing}
          value={data}
          onChange={(e) => onChange(e.target.value)}
          as="p"
          multiline
        />
      );
    }
    return null;
  }

  return (
    <div style={{ textAlign: 'left' }}>
      {isEditing && (
        <div className="mb-2 text-end">
          <button className="btn btn-success btn-sm" onClick={handleAddItem}>+ Agregar despliegue</button>
        </div>
      )}
      <Accordion flush defaultActiveKey="0">
        {data.map((item, index) => (
          <Accordion.Item eventKey={String(index)} key={index}>
            <Accordion.Header>
              <EditableField
                isEditing={isEditing}
                value={item.title}
                onChange={(e) => handleItemChange(index, 'title', e.target.value)}
                onClick={(e) => {
                  if (isEditing) {
                    e.preventDefault();
                    e.stopPropagation();
                  }
                }}
                as="span"
              />
              {isEditing && (
                <button className="btn btn-danger btn-sm ms-2" onClick={(e) => { e.stopPropagation(); handleRemoveItem(index); }}>
                  Eliminar
                </button>
              )}
            </Accordion.Header>
            <Accordion.Body>
              <EditableField
                isEditing={isEditing}
                value={item.content}
                onChange={(e) => handleItemChange(index, 'content', e.target.value)}
                as="div"
                multiline
                rows={5}
              />
            </Accordion.Body>
          </Accordion.Item>
        ))}
      </Accordion>
    </div>
  );
}

export default CompanyInfoAccordion;
