import React from 'react';
import { Container, Row, Col, Card, Button } from 'react-bootstrap';
import { Line, Bar } from 'react-chartjs-2';
import EditableField from './EditableField';


const PIBView = ({ isEditing, pibData, onDataChange }) => {
  // Estado global para el ndice de grfica seleccionada por sector
  const [selectedCharts, setSelectedCharts] = React.useState({});

  // Funcin para obtener el ndice seleccionado de un sector
  const getSelectedChartIndex = (sectorKey, charts) => {
    if (selectedCharts[sectorKey] === undefined || selectedCharts[sectorKey] >= charts.length) {
      return 0;
    }
    return selectedCharts[sectorKey];
  };

  // Mtodos para manipular las grficas dentro del sector
  const handleSelectChart = (sectorKey, idx) => {
    setSelectedCharts(prev => ({ ...prev, [sectorKey]: idx }));
  };
  const handleAddChart = (sectorKey, charts) => {
    const newChart = {
      name: `Nueva grfica ${charts.length + 1}`,
      chartData: {
        labels: ["2025"],
        datasets: [{
          type: 'line',
          label: "Dato de Ejemplo",
          data: [100],
          borderColor: "rgba(54, 162, 235, 1)",
          yAxisID: 'y',
        }]
      },
      analysis: { behavior: '', growth: '' }
    };
    const newCharts = [...charts, newChart];
    const path = `pib.sectors.${sectorKey}.charts`;
    onDataChange(path, newCharts);
    setSelectedCharts(prev => ({ ...prev, [sectorKey]: newCharts.length - 1 }));
  };
  const handleRemoveChart = (sectorKey, charts, idx) => {
    if (charts.length === 1) return;
    if (window.confirm('Seguro que deseas eliminar esta grfica?')) {
      const newCharts = charts.filter((_, i) => i !== idx);
      const path = `pib.sectors.${sectorKey}.charts`;
      onDataChange(path, newCharts);
      setSelectedCharts(prev => ({ ...prev, [sectorKey]: 0 }));
    }
  };
  const handleChartNameChange = (sectorKey, charts, idx, value) => {
    const newCharts = charts.map((c, i) => i === idx ? { ...c, name: value } : c);
    const path = `pib.sectors.${sectorKey}.charts`;
    onDataChange(path, newCharts);
  };

  const handlePibDataChange = (sectorKey, field, value) => {
    const path = `pib.sectors.${sectorKey}.${field}`;
    onDataChange(path, value);
  };

  // Mtodos para edicin avanzada de la grfica
  const handleChartTypeChange = (sectorKey, dsIndex, value) => {
    const newChartData = JSON.parse(JSON.stringify(pibData.sectors[sectorKey].chartData));
    newChartData.datasets[dsIndex].type = value;
    const path = `pib.sectors.${sectorKey}.chartData`;
    onDataChange(path, newChartData);
  };

  const handleChartColorChange = (sectorKey, dsIndex, value) => {
    const newChartData = JSON.parse(JSON.stringify(pibData.sectors[sectorKey].chartData));
    newChartData.datasets[dsIndex].borderColor = value;
    if (newChartData.datasets[dsIndex].type === 'bar') {
      newChartData.datasets[dsIndex].backgroundColor = value + '55';
    } else {
      newChartData.datasets[dsIndex].backgroundColor = value + '33';
    }
    const path = `pib.sectors.${sectorKey}.chartData`;
    onDataChange(path, newChartData);
  };

  const handleAddDataset = (sectorKey) => {
    const newChartData = JSON.parse(JSON.stringify(pibData.sectors[sectorKey].chartData));
    newChartData.datasets.push({
      type: 'line',
      label: `Serie ${newChartData.datasets.length + 1}`,
      data: newChartData.labels.map(() => 0),

      borderColor: `hsl(${Math.floor(Math.random()*360)},70%,50%)`,
      backgroundColor: `hsla(${Math.floor(Math.random()*360)},70%,50%,0.3)`
    });
    const path = `pib.sectors.${sectorKey}.chartData`;
    onDataChange(path, newChartData);
  };

  const handleRemoveDataset = (sectorKey, dsIndex) => {
    const newChartData = JSON.parse(JSON.stringify(pibData.sectors[sectorKey].chartData));
    if (newChartData.datasets.length <= 1) return;
    newChartData.datasets.splice(dsIndex, 1);
    const path = `pib.sectors.${sectorKey}.chartData`;
    onDataChange(path, newChartData);
  };

  const handleAddYear = (sectorKey) => {
    const newChartData = JSON.parse(JSON.stringify(pibData.sectors[sectorKey].chartData));
    newChartData.labels.push('Nuevo Ao');
    newChartData.datasets.forEach(ds => ds.data.push(0));
    const path = `pib.sectors.${sectorKey}.chartData`;
    onDataChange(path, newChartData);
  };

  const handleRemoveYear = (sectorKey, yearIdx) => {
    const newChartData = JSON.parse(JSON.stringify(pibData.sectors[sectorKey].chartData));
    if (newChartData.labels.length <= 1) return;
    newChartData.labels.splice(yearIdx, 1);
    newChartData.datasets.forEach(ds => ds.data.splice(yearIdx, 1));
    const path = `pib.sectors.${sectorKey}.chartData`;
    onDataChange(path, newChartData);
  };

  // Nueva funcin: eliminar ao por etiqueta en la grfica seleccionada del sector
  const handleRemoveYearByLabel = (sectorKey) => {
    const sector = pibData.sectors[sectorKey];
    const charts = Array.isArray(sector.charts) ? sector.charts : (sector.chartData ? [{
      name: 'Grfica principal',
      chartData: sector.chartData,
      analysis: sector.analysis || { behavior: '', growth: '' }
    }] : []);
    const selectedIdx = getSelectedChartIndex(sectorKey, charts);
    const labels = charts[selectedIdx]?.chartData?.labels || [];
    if (!labels.length) {
      alert('No hay aos para eliminar.');
      return;
    }
    const labelToRemove = window.prompt('Ingrese la etiqueta (ao) a eliminar (ej: 2023):');
    if (labelToRemove === null) return; // cancelado
    const yearIdx = labels.indexOf(labelToRemove);
    if (yearIdx === -1) {
      alert('Etiqueta no encontrada.');
      return;
    }
    if (!window.confirm(`Eliminar la etiqueta "${labelToRemove}" y sus datos correspondientes?`)) return;
    const newCharts = charts.map((c, i) => i === selectedIdx ? {
      ...c,
      chartData: {
        ...c.chartData,
        labels: c.chartData.labels.filter((_, j) => j !== yearIdx),
        datasets: c.chartData.datasets.map(ds => ({
          ...ds,
          data: ds.data.filter((_, j) => j !== yearIdx)
        }))
      }
    } : c);
    const path = `pib.sectors.${sectorKey}.charts`;
    onDataChange(path, newCharts);
  };

  const handleChartDataChange = (sectorKey, datasetIndex, dataIndex, value) => {
    const newChartData = JSON.parse(JSON.stringify(pibData.sectors[sectorKey].chartData));
    newChartData.datasets[datasetIndex].data[dataIndex] = parseFloat(value) || 0;
    const path = `pib.sectors.${sectorKey}.chartData`;
    onDataChange(path, newChartData);
  };

  const handleChartLabelChange = (sectorKey, index, value) => {
    const newChartData = JSON.parse(JSON.stringify(pibData.sectors[sectorKey].chartData));
    newChartData.labels[index] = value;
    const path = `pib.sectors.${sectorKey}.chartData`;
    onDataChange(path, newChartData);
  };

  const handleAddSector = () => {
    const newSectorKey = `sector_${Date.now()}`;
    const newSector = {
      title: "Nuevo Sector",
      text: "Descripcin del nuevo sector.",
      chartData: {
        labels: ["2025"],
        datasets: [{
          label: "PIB",
          data: [0],
          borderColor: "rgba(75, 192, 192, 1)",
        }]
      }
    };
    const path = `pib.sectors.${newSectorKey}`;
    onDataChange(path, newSector);
  };

  const handleRemoveSector = (sectorKey) => {
    if (window.confirm('Ests seguro de que quieres eliminar este sector?')) {
      const newSectors = { ...pibData.sectors };
      delete newSectors[sectorKey];
      onDataChange('pib.sectors', newSectors);
    }
  };

  if (!pibData) {
    return <p>Cargando datos del PIB...</p>;
  }

  // Opciones dinmicas para Chart.js segn los datos
  const getChartOptions = (chartData) => {
    // Ejes separados para Produccin (izquierda) y Valor (derecha)
    let prodMax = null, prodMin = null, valorMax = null, valorMin = null;
    if (chartData && chartData.datasets) {
      if (chartData.datasets[0]) {
        chartData.datasets[0].data.forEach(val => {
          if (typeof val === 'number') {
            if (prodMax === null || val > prodMax) prodMax = val;
            if (prodMin === null || val < prodMin) prodMin = val;
          }
        });
      }
      if (chartData.datasets[1]) {
        chartData.datasets[1].data.forEach(val => {
          if (typeof val === 'number') {
            if (valorMax === null || val > valorMax) valorMax = val;
            if (valorMin === null || val < valorMin) valorMin = val;
          }
        });
      }
    }
    // Ajustar el mnimo para que no sea 0 si todos los datos son positivos y altos
    // Forzar enteros en el eje de Produccin
    const yMin = (prodMin !== null) ? Math.floor(prodMin) : 0;
    const yMax = (prodMax !== null) ? Math.ceil(prodMax) : 1;
    let yStep = 1;
    if (prodMax !== null && prodMin !== null) {
      const range = yMax - yMin;
      if (range > 10) {
        yStep = Math.max(1, Math.round(range / 5));
      } else {
        yStep = 1;
      }
    }
    // Forzar enteros en el eje de Valor, pero si los datos son decimales grandes, usar stepSize adecuado
    let y1Min = (valorMin !== null) ? Math.floor(valorMin) : 0;
    let y1Max = (valorMax !== null) ? Math.ceil(valorMax) : 1;
    // Forzar stepSize a 1 para el eje de Valor
    let y1Step = 1;
    return {
      responsive: true,
      interaction: { mode: 'index', intersect: false },
      elements: { point: { radius: 5 } },
      plugins: { legend: { position: 'top' } },
      scales: {
        y: {
          type: 'linear',
          position: 'left',
          title: { display: true, text: 'Produccin' },
          beginAtZero: false,
          min: yMin,
          max: yMax,
          ticks: {
            stepSize: yStep,
            callback: function(value) { return Number.isInteger(value) ? value : Math.round(value); }
          }
        },
        y1: {
          type: 'linear',
          position: 'right',
          title: { display: true, text: 'Valor' },
          beginAtZero: false,
          min: y1Min,
          max: y1Max,
          grid: { drawOnChartArea: false },
          ticks: {
            stepSize: 1,
            callback: function(value) { return Math.round(value); }
          }
        }
      }
    };
  };

  return (
    <Container>
      <header className="text-center mb-5">
        <EditableField
          isEditing={isEditing}
          value={pibData.title}
          onChange={(e) => onDataChange('pib.title', e.target.value)}
          as="h1"
        />
        <EditableField
          isEditing={isEditing}
          value={pibData.intro}
          onChange={(e) => onDataChange('pib.intro', e.target.value)}
          as="p"
          multiline
          className="lead"
        />
      </header>

      {isEditing && (
        <div className="text-center mb-4">
          <Button variant="success" onClick={handleAddSector}>+ Aadir Sector PIB</Button>
        </div>
      )}

      <Row>
        {Object.keys(pibData.sectors).map(sectorKey => {
          const sector = pibData.sectors[sectorKey];
          let charts = Array.isArray(sector.charts) ? sector.charts : (sector.chartData ? [{
            name: 'Grfica principal',
            chartData: sector.chartData,
            analysis: sector.analysis || { behavior: '', growth: '' }
          }] : []);
          const selectedChartIndex = getSelectedChartIndex(sectorKey, charts);
          const safeChart = charts[selectedChartIndex] || charts[0];

          return (
            <Col md={12} key={sectorKey} className="mb-4">
              <Card>
                <Card.Header>
                  <EditableField
                    isEditing={isEditing}
                    value={sector.title}
                    onChange={(e) => handlePibDataChange(sectorKey, 'title', e.target.value)}
                    as="h5"
                  />
                </Card.Header>
                <Card.Body>
                  {/* Selector de grficas y botones de agregar/eliminar */}
                  <div className="mb-3 d-flex align-items-center gap-2">
                    <select
                      className="form-select w-auto"
                      value={selectedChartIndex}
                      onChange={e => handleSelectChart(sectorKey, Number(e.target.value))}
                    >
                      {charts.map((c, i) => (
                        <option key={i} value={i}>{c.name}</option>
                      ))}
                    </select>
                    {isEditing && (
                      <>
                        <Button variant="success" size="sm" onClick={() => handleAddChart(sectorKey, charts)}>+ Agregar Grfica</Button>
                        {charts.length > 1 && (
                          <Button variant="danger" size="sm" onClick={() => handleRemoveChart(sectorKey, charts, selectedChartIndex)}>- Eliminar</Button>
                        )}
                        <input
                          type="text"
                          className="form-control ms-2"
                          style={{width:180,display:'inline-block'}}
                          value={charts[selectedChartIndex]?.name || ''}
                          onChange={e => handleChartNameChange(sectorKey, charts, selectedChartIndex, e.target.value)}
                          placeholder="Nombre de la grfica"
                        />
                        {/* BOTN VISIBLE: Quitar Ao por etiqueta (barra de controles) */}
                        <Button
                          variant="warning"
                          size="sm"
                          onClick={() => handleRemoveYearByLabel(sectorKey)}
                          title="Quitar ao por etiqueta"
                          style={{marginLeft:8}}
                        >
                          Quitar Ao/Dato
                        </Button>
                      </>
                    )}
                  </div>
                  <Row>
                    <Col md={8}>
                      {safeChart && safeChart.chartData && safeChart.chartData.datasets.some(ds => ds.type === 'bar') ? (
                        <Bar data={{
                          ...safeChart.chartData,
                          datasets: safeChart.chartData.datasets.map((ds, i) => ({
                            ...ds,
                            yAxisID: i === 1 ? 'y1' : 'y'
                          }))
                        }} options={{
                          ...getChartOptions(safeChart.chartData),
                          scales: {
                            ...getChartOptions(safeChart.chartData).scales,
                            y: {
                              ...getChartOptions(safeChart.chartData).scales.y,
                              ticks: {
                                ...getChartOptions(safeChart.chartData).scales.y.ticks,
                                stepSize: 1,
                                callback: function(value) { return Math.round(value); }
                              }
                            },
                            y1: {
                              ...getChartOptions(safeChart.chartData).scales.y1,
                              ticks: {
                                ...getChartOptions(safeChart.chartData).scales.y1.ticks,
                                stepSize: 1,
                                callback: function(value) { return Math.round(value); }
                              }
                            }
                          }
                        }} />
                      ) : (
                        <Line data={{
                          ...safeChart.chartData,
                          datasets: safeChart.chartData.datasets.map((ds, i) => ({
                            ...ds,
                            yAxisID: i === 1 ? 'y1' : 'y'
                          }))
                        }} options={{
                          ...getChartOptions(safeChart.chartData),
                          scales: {
                            ...getChartOptions(safeChart.chartData).scales,
                            y: {
                              ...getChartOptions(safeChart.chartData).scales.y,
                              ticks: {
                                ...getChartOptions(safeChart.chartData).scales.y.ticks,
                                stepSize: 1,
                                callback: function(value) { return Math.round(value); }
                              }
                            },
                            y1: {
                              ...getChartOptions(safeChart.chartData).scales.y1,
                              ticks: {
                                ...getChartOptions(safeChart.chartData).scales.y1.ticks,
                                stepSize: 1,
                                callback: function(value) { return Math.round(value); }
                              }
                            }
                          }
                        }} />
                      )}
                    </Col>
                    <Col md={4}>
                      <EditableField
                        isEditing={isEditing}
                        value={safeChart?.text || sector.text}
                        onChange={(e) => handlePibDataChange(sectorKey, 'text', e.target.value)}
                        as="p"
                        multiline
                        rows={6}
                      />
                      <h6 className="mt-3">Anlisis del Comportamiento</h6>
                      <EditableField
                        isEditing={isEditing}
                        value={safeChart?.analysis?.behavior || ''}
                        onChange={e => {
                          const newCharts = charts.map((c, i) => i === selectedChartIndex ? { ...c, analysis: { ...c.analysis, behavior: e.target.value } } : c);
                          const path = `pib.sectors.${sectorKey}.charts`;
                          onDataChange(path, newCharts);
                        }}
                        as="p"
                        multiline
                        rows={3}
                      />
                      <h6 className="mt-3">Anlisis del Crecimiento</h6>
                      <EditableField
                        isEditing={isEditing}
                        value={safeChart?.analysis?.growth || ''}
                        onChange={e => {
                          const newCharts = charts.map((c, i) => i === selectedChartIndex ? { ...c, analysis: { ...c.analysis, growth: e.target.value } } : c);
                          const path = `pib.sectors.${sectorKey}.charts`;
                          onDataChange(path, newCharts);
                        }}
                        as="p"
                        multiline
                        rows={3}
                      />
                    </Col>
                  </Row>
                  {isEditing && safeChart && safeChart.chartData && (
                    <div className="mt-4">
                      <h6>Editar datos de la grfica</h6>
                      <div className="table-responsive">
                        <table className="table table-bordered text-center">
                          <thead>
                            <tr>
                              <th>Etiqueta (Ao)</th>
                              <th>Produccin</th>
                              <th>Valor</th>
                              {safeChart.chartData.datasets.length > 2 && safeChart.chartData.datasets.slice(2).map((dataset, dsIndex) => (
                                <th key={dsIndex + 2}>
                                  <div className="d-flex flex-column align-items-center gap-1">
                                    <input
                                      type="text"
                                      className="form-control form-control-sm mb-1"
                                      style={{minWidth:80}}
                                      value={dataset.label}
                                      onChange={e => {
                                        const newCharts = charts.map((c, i) => i === selectedChartIndex ? {
                                          ...c,
                                          chartData: {
                                            ...c.chartData,
                                            datasets: c.chartData.datasets.map((ds, j) => j === dsIndex + 2 ? { ...ds, label: e.target.value } : ds)
                                          }
                                        } : c);
                                        const path = `pib.sectors.${sectorKey}.charts`;
                                        onDataChange(path, newCharts);
                                      }}
                                    />
                                  </div>
                                </th>
                              ))}
                              <th>
                                <Button size="sm" variant="success" onClick={() => {
                                  const newCharts = charts.map((c, i) => i === selectedChartIndex ? {
                                    ...c,
                                    chartData: {
                                      ...c.chartData,
                                      datasets: [
                                        ...c.chartData.datasets,
                                        {
                                          type: 'line',
                                          label: `Serie ${c.chartData.datasets.length + 1}`,
                                          data: c.chartData.labels.map(() => 0),
                                          borderColor: `hsl(${Math.floor(Math.random()*360)},70%,50%)`,
                                          backgroundColor: `hsla(${Math.floor(Math.random()*360)},70%,50%,0.3)`
                                        }
                                      ]
                                    }
                                  } : c);
                                  const path = `pib.sectors.${sectorKey}.charts`;
                                  onDataChange(path, newCharts);
                                }}>+ Columna</Button>
                              </th>
                            </tr>
                          </thead>
                          <tbody>
                            {safeChart.chartData.labels.map((label, index) => (
                              <tr key={index}>
                                <td>
                                  <div className="d-flex flex-column align-items-center gap-1">
                                    <input
                                      type="text"
                                      className="form-control"
                                      value={label}
                                      onChange={e => {
                                        const newCharts = charts.map((c, i) => i === selectedChartIndex ? {
                                          ...c,
                                          chartData: {
                                            ...c.chartData,
                                            labels: c.chartData.labels.map((l, j) => j === index ? e.target.value : l)
                                          }
                                        } : c);
                                        const path = `pib.sectors.${sectorKey}.charts`;
                                        onDataChange(path, newCharts);
                                      }}
                                    />
                                    {safeChart.chartData.labels.length > 1 && (
                                      <Button size="sm" variant="danger" onClick={() => {
                                        if (window.confirm('Seguro que deseas eliminar este ao?')) {
                                          const newCharts = charts.map((c, i) => i === selectedChartIndex ? {
                                            ...c,
                                            chartData: {
                                              ...c.chartData,
                                              labels: c.chartData.labels.filter((_, j) => j !== index),
                                              datasets: c.chartData.datasets.map(ds => ({
                                                ...ds,
                                                data: ds.data.filter((_, j) => j !== index)
                                              }))
                                            }
                                          } : c);
                                          const path = `pib.sectors.${sectorKey}.charts`;
                                          onDataChange(path, newCharts);
                                        }
                                      }}>Quitar</Button>
                                    )}
                                  </div>
                                </td>
                                {/* Produccin */}
                                <td>
                                  <input
                                    type="number"
                                    className="form-control"
                                    value={safeChart.chartData.datasets[0]?.data[index] ?? ''}
                                    onChange={e => {
                                      const newCharts = charts.map((c, i) => i === selectedChartIndex ? {
                                        ...c,
                                        chartData: {
                                          ...c.chartData,
                                          datasets: c.chartData.datasets.map((ds, j) => j === 0 ? {
                                            ...ds,
                                            data: ds.data.map((d, k) => k === index ? parseFloat(e.target.value) || 0 : d)
                                          } : ds)
                                        }
                                      } : c);
                                      const path = `pib.sectors.${sectorKey}.charts`;
                                      onDataChange(path, newCharts);
                                    }}
                                  />
                                </td>
                                {/* Valor */}
                                <td>
                                  <input
                                    type="number"
                                    className="form-control"
                                    value={safeChart.chartData.datasets[1]?.data[index] ?? ''}
                                    onChange={e => {
                                      const newCharts = charts.map((c, i) => i === selectedChartIndex ? {
                                        ...c,
                                        chartData: {
                                          ...c.chartData,
                                          datasets: c.chartData.datasets.map((ds, j) => j === 1 ? {
                                            ...ds,
                                            data: ds.data.map((d, k) => k === index ? parseFloat(e.target.value) || 0 : d)
                                          } : ds)
                                        }
                                      } : c);
                                      const path = `pib.sectors.${sectorKey}.charts`;
                                      onDataChange(path, newCharts);
                                    }}
                                  />
                                </td>
                                {/* Otras series dinmicas */}
                                {safeChart.chartData.datasets.length > 2 && safeChart.chartData.datasets.slice(2).map((dataset, dsIndex) => (
                                  <td key={dsIndex + 2}>
                                    <input
                                      type="number"
                                      className="form-control"
                                      value={dataset.data[index]}
                                      onChange={e => {
                                        const newCharts = charts.map((c, i) => i === selectedChartIndex ? {
                                          ...c,
                                          chartData: {
                                            ...c.chartData,
                                            datasets: c.chartData.datasets.map((ds, j) => j === dsIndex + 2 ? {
                                              ...ds,
                                              data: ds.data.map((d, k) => k === index ? parseFloat(e.target.value) || 0 : d)
                                            } : ds)
                                        }
                                      } : c);
                                      const path = `pib.sectors.${sectorKey}.charts`;
                                      onDataChange(path, newCharts);
                                    }}
                                  />
                                </td>
                                ))}
                              </tr>
                            ))}
                          </tbody>
                        </table>
                      </div>
                      <div className="d-flex justify-content-end mt-2 gap-2">
                        <Button size="sm" variant="success" onClick={() => {
                          const newCharts = charts.map((c, i) => i === selectedChartIndex ? {
                            ...c,
                            chartData: {
                              ...c.chartData,
                              labels: [...c.chartData.labels, 'Nuevo Ao'],
                              datasets: c.chartData.datasets.map(ds => ({
                                ...ds,
                                data: [...ds.data, 0]
                              }))
                            }
                          } : c);
                          const path = `pib.sectors.${sectorKey}.charts`;
                          onDataChange(path, newCharts);
                        }}>
                          Aadir Ao/Dato
                        </Button>
                        <Button
                          size="sm"
                          variant="warning"
                          onClick={() => handleRemoveYearByLabel(sectorKey)}
                          title="Selecciona el ao que deseas eliminar"
                        >
                          Quitar Ao/Dato
                        </Button>


                      </div>
                    </div>
                  )}
                </Card.Body>
                {isEditing && (
                  <Card.Footer>
                    <Button variant="danger" onClick={() => handleRemoveSector(sectorKey)}>
                      Quitar Sector
                    </Button>
                  </Card.Footer>
                )}
              </Card>
            </Col>
          );
        })}
      </Row>
    </Container>
  );
};

export default PIBView;
