// Main App Component
function App() {
  const [activeSector, setActiveSector] = useState('colombia');
  const [isEditing, setIsEditing] = useState(false);
  const [pageData, setPageData] = useState(initialEconomicData);
  const [backupData, setBackupData] = useState(null);
  const [currentView, setCurrentView] = useState('main'); // 'main', 'sectores', 'pib'

  useEffect(() => {
    try {
      const savedData = localStorage.getItem('economicData');
      if (savedData) {
        setPageData(JSON.parse(savedData));
      }
    } catch (error) {
      console.error("Error loading data from localStorage", error);
      setPageData(initialEconomicData);
    }
  }, []);

  const handleToggleEdit = () => {
    if (isEditing) {
      if (window.confirm('Ests seguro de que quieres guardar todos los cambios?')) {
        try {
          localStorage.setItem('economicData', JSON.stringify(pageData));
          setIsEditing(false);
          setBackupData(null);
        } catch (error) {
          console.error("Error saving data to localStorage", error);
          alert("No se pudieron guardar los cambios.");
        }
      }
    } else {
      // Solicitar contrasea antes de editar
  const clave = window.prompt('Ingrese la contrasea para editar:');
      if (clave === '123789') {
        setBackupData(JSON.parse(JSON.stringify(pageData)));
        setIsEditing(true);
      } else if (clave !== null) {
        alert('Contrasea incorrecta. No se puede activar el modo edicin.');
      }
    }
  };

  const handleCancelEdit = () => {
    if (window.confirm('Ests seguro de que quieres descartar los cambios?')) {
      setPageData(backupData);
      setIsEditing(false);
      setBackupData(null);
    }
  };

  const handleResetData = () => {
    if (window.confirm('Ests seguro de que quieres restablecer todos los datos a su estado original? Se perdern todos los cambios no guardados.')) {
      localStorage.removeItem('economicData');
      setPageData(initialEconomicData); // Recargar datos iniciales en el estado
      setIsEditing(false); // Salir del modo edicin
      setBackupData(null);
      // Opcional: recargar la pgina para una limpieza completa
      // window.location.reload();
    }
  };

  const handleDataChange = (path, value) => {
    setPageData(prevData => {
      const newState = JSON.parse(JSON.stringify(prevData));
      const keys = path.split('.');
      let current = newState;
      for (let i = 0; i < keys.length - 1; i++) {
        current = current[keys[i]];
      }
      current[keys[keys.length - 1]] = value;
      return newState;
    });
  };

  const renderSectoresView = () => (
    <>
      <Navbar bg="dark" variant="dark" expand="lg" sticky="top" className="no-print">
        <Container>
          <Navbar.Brand onClick={() => setCurrentView('main')} style={{cursor: 'pointer'}}>Anlisis Econmico de Colombia</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              {Object.keys(pageData).filter(k => k !== 'pib').map((sectorKey) => (
                <Nav.Link
                  key={sectorKey}
                  onClick={() => setActiveSector(sectorKey)}
                  active={activeSector === sectorKey}
                >
                  <EditableField 
                    isEditing={isEditing} 
                    value={pageData[sectorKey].name} 
                    onChange={(e) => handleDataChange(`${sectorKey}.name`, e.target.value)} 
                    as="span"
                  />
                </Nav.Link>
              ))}
            </Nav>
            <Button variant="info" onClick={handleToggleEdit} className="me-2">
              {isEditing ? 'Guardar Cambios' : 'Editar Contenido'}
            </Button>
            {isEditing && (
              <>
                <Button variant="secondary" onClick={handleCancelEdit} className="me-2">
                  Cancelar Edicin
                </Button>
                <Button variant="danger" onClick={handleResetData}>
                  Restablecer a Original
                </Button>
              </>
            )}
          </Navbar.Collapse>
        </Container>
      </Navbar>

      <Container className="mt-4" id="page-content">
        {activeSector === 'colombia' ? (
          <ColombiaView 
            isEditing={isEditing}
            pageData={pageData.colombia}
            onDataChange={handleDataChange}
          />
        ) : (
          <SectorView 
            sector={pageData[activeSector]} 
            sectorKey={activeSector} 
            isEditing={isEditing}
            onDataChange={handleDataChange}
          />
        )}
      </Container>
    </>
  );

  const renderContent = () => {
    switch (currentView) {
      case 'sectores':
        return renderSectoresView();
      case 'pib':
        return (
          <>
            <Navbar bg="dark" variant="dark" expand="lg" sticky="top" className="no-print">
              <Container>
                <Navbar.Brand onClick={() => setCurrentView('main')} style={{cursor: 'pointer'}}>Anlisis Econmico de Colombia</Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                  <Nav className="me-auto">
                    <Nav.Link onClick={() => setCurrentView('main')}>Inicio</Nav.Link>
                  </Nav>
                  <Button variant="info" onClick={handleToggleEdit} className="me-2">
                    {isEditing ? 'Guardar Cambios' : 'Editar Contenido'}
                  </Button>
                  {isEditing && (
                    <>
                      <Button variant="secondary" onClick={handleCancelEdit} className="me-2">
                        Cancelar Edicin
                      </Button>
                      <Button variant="danger" onClick={handleResetData}>
                        Restablecer a Original
                      </Button>
                    </>
                  )}
                </Navbar.Collapse>
              </Container>
            </Navbar>
            <PIBView
              isEditing={isEditing}
              pibData={pageData.pib}
              onDataChange={handleDataChange}
            />
          </>
        );
      case 'main':
      default:
        return (
          <Container className="d-flex flex-column justify-content-center align-items-center" style={{ height: '100vh' }}>
            <h1 className="mb-4">Anlisis Econmico de Colombia</h1>
            <img 
              src={process.env.PUBLIC_URL + '/imagenes/MAPA-COLOMBIA.JPG'} 
              alt="Mapa de Colombia" 
              style={{
                maxWidth: '420px',
                width: '100%',
                height: 'auto',
                marginBottom: '2rem',
                borderRadius: '12px',
                boxShadow: '0 4px 24px rgba(0,0,0,0.15)'
              }}
            />
            <Row>
              <Col md={6} className="text-center">
                <Button variant="primary" size="lg" onClick={() => setCurrentView('sectores')} style={{width: '200px', height: '80px', fontSize: '1.5rem'}}>
                  Sectores
                </Button>
              </Col>
              <Col md={6} className="text-center">
                <Button variant="secondary" size="lg" onClick={() => setCurrentView('pib')} style={{width: '200px', height: '80px', fontSize: '1.5rem'}}>
                  PIB
                </Button>
              </Col>
            </Row>
          </Container>
        );
    }
  };

  return (
    <div className="App">
      {renderContent()}
      <footer className="bg-dark text-white text-center p-3 mt-5 no-print">
        <p>&copy; CS-LB-VM-1562053-65-09-42</p>
      </footer>
    </div>
  );
}

// View for "Colombia" section
const ColombiaView = ({ isEditing, pageData, onDataChange }) => {
  const [activeSubView, setActiveSubView] = useState('petroleo');
  const [selectedEvent, setSelectedEvent] = useState(null);
  const [showDownloadModal, setShowDownloadModal] = useState(false);
  const [showMapModal, setShowMapModal] = useState(false);
  const [selectedMap, setSelectedMap] = useState(null);

  const sectorData = pageData.data;
  const data = sectorData[activeSubView];

  const handleSelectView = (view) => {
    setActiveSubView(view);
    setSelectedEvent(null);
    setShowMapModal(false);
    setSelectedMap(null);
  };

  const handleOpenMapModal = (map) => {
    setSelectedMap(map);
    setShowMapModal(true);
  };

  const handleCloseMapModal = () => {
    setShowMapModal(false);
    setSelectedMap(null);
  };

  const handleTimelineClick = (item) => {
    setSelectedEvent(selectedEvent?.year === item.year ? null : item);
  };

  return (
    <>
      <Navbar bg="light" variant="light" expand="lg" sticky="top" className="no-print mb-4 sub-navbar">
        <Container>
          <Navbar.Toggle aria-controls="colombia-navbar-nav" />
          <Navbar.Collapse id="colombia-navbar-nav">
            <Nav className="me-auto">
              {Object.keys(sectorData).map((sectorKey) => (
                <Nav.Link
                  key={sectorKey}
                  onClick={() => handleSelectView(sectorKey)}
                  active={activeSubView === sectorKey}
                >
                   <EditableField 
                    isEditing={isEditing} 
                    value={sectorData[sectorKey].name} 
                    onChange={(e) => onDataChange(`colombia.data.${sectorKey}.name`, e.target.value)} 
                    as="span"
                  />
                </Nav.Link>
              ))}
              <Nav.Link
                key="mapas"
                onClick={() => handleSelectView('mapas')}
                active={activeSubView === 'mapas'}
              >
                Mapas
              </Nav.Link>
            </Nav>
            <Button variant="outline-primary" onClick={() => setShowDownloadModal(true)}>Descargar</Button>
          </Navbar.Collapse>
        </Container>
      </Navbar>

      {activeSubView !== 'mapas' ? (
        <ActivityView 
          data={data} 
          selectedEvent={selectedEvent} 
          onTimelineClick={handleTimelineClick} 
          isEditing={isEditing}
          onDataChange={(path, value) => onDataChange(`colombia.data.${activeSubView}.${path}`, value)}
        />
      ) : (
        <MapasView mapasData={mapasData} selectedMap={selectedMap} showMapModal={showMapModal} onOpenModal={handleOpenMapModal} onCloseModal={handleCloseMapModal} />
      )}

      <Modal show={showDownloadModal} onHide={() => setShowDownloadModal(false)} centered>
        <Modal.Header closeButton>
          <Modal.Title>Descargar Contenido</Modal.Title>
        </Modal.Header>
        <Modal.Body className="text-center">
          <p>Selecciona el formato en el que deseas descargar el anlisis.</p>
          <Button variant="danger" className="me-2" onClick={() => { setShowDownloadModal(false); window.print(); }}>
            <i className="fas fa-file-pdf me-2"></i>Descargar como PDF
          </Button>
        </Modal.Body>
      </Modal>
    </>
  );
};

// View for other economic sectors
const SectorView = ({ sector, sectorKey, isEditing, onDataChange }) => {
    const [selectedRegion, setSelectedRegion] = useState(null);
    const [selectedActivity, setSelectedActivity] = useState(null);
    const [regionViewMode, setRegionViewMode] = useState('activities'); // 'activities' or 'maps'
  
    const handleRegionClick = (regionKey) => {
      setSelectedRegion(regionKey);
      setSelectedActivity(null);
      setRegionViewMode('activities'); // Reset to default view
    };
  
    const handleActivityClick = (activityKey) => {
      setSelectedActivity(activityKey);
    };

    const handleAddActivity = (regionKey) => {
      const newActivityKey = `actividad_${Date.now()}`;
      const newActivity = {
        name: "Nueva Actividad",
        pageTitle: "Anlisis de Nueva Actividad",
        impactImage: "",
        chartImage: "",
        representativeCompany: {
          name: "Empresa Ejemplo",
          logo: "",
          backgroundImage: "",
          description: [{ title: "Introduccin", content: "Descripcin de la nueva empresa." }]
        },
        chartData: {
          labels: ["2025"],
          datasets: [{
            type: 'line',
            label: "Dato de Ejemplo",
            data: [100],
            borderColor: "rgba(54, 162, 235, 1)",
            yAxisID: 'y',
          }]
        },
        analysis: { behavior: "", growth: "" },
        impact: { social: "", political: "", economic: "" },
        timeline: [{ year: "2025", event: "Evento inicial", detailedEvent: "" }]
      };

      const path = `${sectorKey}.regions.${regionKey}.activities.${newActivityKey}`;
      onDataChange(path, newActivity);
    };

    const handleRemoveActivity = (regionKey, activityKey) => {
      if (window.confirm(`Ests seguro de que quieres eliminar la actividad "${sector.regions[regionKey].activities[activityKey].name}"? Esta accin no se puede deshacer.`)) {
        const newActivities = { ...sector.regions[regionKey].activities };
        delete newActivities[activityKey];
        
        const path = `${sectorKey}.regions.${regionKey}.activities`;
        onDataChange(path, newActivities);
        setSelectedActivity(null); // Deseleccionar si estaba activa
      }
    };
  
    const regionColorMap = {
      andina: 'andina',
      amazonia: 'amazonia',
      caribe: 'caribe',
      insular: 'insular',
      orinoquia: 'orinoquia',
      pacifico: 'pacifico',
    };

    const activityColorMap = {
      agricultura: 'success',
      ganaderia: 'secondary',
      silvicultura: 'info',
      mineria: 'dark',
      pesca: 'primary',
      recoleccion: 'warning',
      industria: 'danger',
      construccion: 'secondary',
      energia: 'info',
      industria_artesanal: 'warning',
      construccion_local: 'secondary',
      agroindustria: 'success',
      mineria_energia: 'dark',
      mineria_pesca: 'primary',
      comercio: 'primary',
      turismo: 'info',
      servicios_empresariales: 'danger'
    };

    return (
      <div className={sectorKey}>
        <header className="text-center mb-5">
          <h1>
            <EditableField 
              isEditing={isEditing} 
              value={sector.name} 
              onChange={(e) => onDataChange(`${sectorKey}.name`, e.target.value)} 
              as="span"
            />
          </h1>
          {sector.intro && 
            <EditableField 
              isEditing={isEditing} 
              value={sector.intro} 
              onChange={(e) => onDataChange(`${sectorKey}.intro`, e.target.value)} 
              as="p"
              multiline
              className="lead"
            />
          }
        </header>
  
        <Row className="mb-4">
          <Col>
            <h4>Selecciona una Regin</h4>
            {Object.keys(sector.regions).map(regionKey => (
              <Button 
                key={regionKey} 
                variant="light"
                onClick={() => handleRegionClick(regionKey)}
                className={`region-button ${regionColorMap[regionKey]} ${selectedRegion === regionKey ? 'active' : ''}`}
              >
                <EditableField 
                  isEditing={isEditing} 
                  value={sector.regions[regionKey].name} 
                  onChange={(e) => onDataChange(`${sectorKey}.regions.${regionKey}.name`, e.target.value)} 
                  as="span"
                />
              </Button>
            ))}
          </Col>
        </Row>
  
        {selectedRegion && (
          <Card className={`region-card ${regionColorMap[selectedRegion]}`}>
            <Card.Header as="h5">
              <EditableField 
                isEditing={isEditing} 
                value={sector.regions[selectedRegion].name} 
                onChange={(e) => onDataChange(`${sectorKey}.regions.${selectedRegion}.name`, e.target.value)} 
                as="span"
              />
            </Card.Header>
            <Card.Body>
              <EditableField 
                isEditing={isEditing} 
                value={sector.regions[selectedRegion].intro} 
                onChange={(e) => onDataChange(`${sectorKey}.regions.${selectedRegion}.intro`, e.target.value)} 
                as="p"
                multiline
              />
              <hr />
              <Nav variant="tabs" defaultActiveKey="activities" onSelect={(selectedKey) => setRegionViewMode(selectedKey)}>
                <Nav.Item>
                  <Nav.Link eventKey="activities">Actividades</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                  <Nav.Link eventKey="maps">Mapas</Nav.Link>
                </Nav.Item>
              </Nav>

              <div className="mt-3">
                {regionViewMode === 'activities' ? (
                  <>
                    <div className="d-flex justify-content-between align-items-center">
                      <h6>Actividades Productivas Representativas</h6>
                      {isEditing && (
                        <Button variant="primary" size="sm" onClick={() => handleAddActivity(selectedRegion)}>
                          + Aadir Actividad
                        </Button>
                      )}
                    </div>
                    
                    {Object.keys(sector.regions[selectedRegion].activities).length > 0 ? (
                      Object.keys(sector.regions[selectedRegion].activities).map(activityKey => (
                        <div key={activityKey} className="d-inline-block position-relative me-2 mb-2">
                          <Button 
                            variant={selectedActivity === activityKey ? (activityColorMap[activityKey] || 'dark') : `outline-${activityColorMap[activityKey] || 'dark'}`}
                            onClick={() => handleActivityClick(activityKey)}
                            className="activity-button"
                          >
                            <EditableField 
                              isEditing={isEditing} 
                              value={sector.regions[selectedRegion].activities[activityKey].name} 
                              onChange={(e) => onDataChange(`${sectorKey}.regions.${selectedRegion}.activities.${activityKey}.name`, e.target.value)} 
                              as="span"
                            />
                          </Button>
                          {isEditing && (
                            <Button 
                              variant="danger" 
                              size="sm"
                              className="position-absolute top-0 start-100 translate-middle rounded-circle p-0 lh-1"
                              style={{ width: '20px', height: '20px' }}
                              onClick={() => handleRemoveActivity(selectedRegion, activityKey)}
                            >
                              &times;
                            </Button>
                          )}
                        </div>
                      ))
                    ) : (
                      <p className="mt-2">No hay actividades definidas. Aade una!</p>
                    )}
        
                    {selectedActivity && sector.regions[selectedRegion].activities[selectedActivity] && (
                      <div className="mt-4">
                        <ActivityView 
                          data={sector.regions[selectedRegion].activities[selectedActivity]} 
                          isEditing={isEditing}
                          onDataChange={(path, value) => onDataChange(`${sectorKey}.regions.${selectedRegion}.activities.${selectedActivity}.${path}`, value)}
                        />
                      </div>
                    )}
                  </>
                ) : (
                  <RegionMapsView 
                    maps={sector.regions[selectedRegion].maps || []}
                    isEditing={isEditing}
                    onDataChange={(newMaps) => onDataChange(`${sectorKey}.regions.${selectedRegion}.maps`, newMaps)}
                  />
                )}
              </div>
            </Card.Body>
          </Card>
        )}
      </div>
    );
  };

const RegionMapsView = ({ maps, isEditing, onDataChange }) => {

  const handleAddMap = () => {
    const newMap = {
      id: `mapa_${Date.now()}`,
      title: 'Nuevo Mapa',
      image: '',
      description: 'Nueva descripcin.'
    };
    onDataChange([...maps, newMap]);
  };

  const handleRemoveMap = (index) => {
    if (window.confirm('Ests seguro de que quieres eliminar este mapa?')) {
      const newMaps = [...maps];
      newMaps.splice(index, 1);
      onDataChange(newMaps);
    }
  };

  const handleMapChange = (index, field, value) => {
    const newMaps = [...maps];
    newMaps[index] = { ...newMaps[index], [field]: value };
    onDataChange(newMaps);
  };

  const handleImageChange = (index, file) => {
    if (file) {
      const reader = new FileReader();
      reader.onloadend = () => {
        handleMapChange(index, 'image', reader.result);
      };
      reader.readAsDataURL(file);
    }
  };

  return (
    <div>
      <div className="d-flex justify-content-between align-items-center mb-3">
        <h6>Mapas de la Regin</h6>
        {isEditing && (
          <Button variant="success" size="sm" onClick={handleAddMap}>
            + Aadir Mapa
          </Button>
        )}
      </div>
      <Row className="g-4">
        {(maps.length > 0 || isEditing) ? maps.map((mapItem, index) => (
          <Col md={6} lg={4} key={mapItem.id || index}>
            <Card className="h-100 shadow-sm">
              {isEditing ? (
                <div className="p-2">
                  <label htmlFor={`map-upload-${index}`} className="btn btn-sm btn-info w-100">Cambiar Imagen</label>
                  <input
                      id={`map-upload-${index}`}
                      type="file"
                      accept="image/*"
                      style={{ display: 'none' }}
                      onChange={(e) => handleImageChange(index, e.target.files[0])}
                  />
                </div>
              ) : null}
              {mapItem.image && <Card.Img variant="top" src={mapItem.image} alt={mapItem.title} />}              <Card.Body>
                <EditableField 
                  isEditing={isEditing}
                  value={mapItem.title}
                  onChange={(e) => handleMapChange(index, 'title', e.target.value)}
                  as="h5"
                  className="card-title"
                />
                <EditableField 
                  isEditing={isEditing}
                  value={mapItem.description}
                  onChange={(e) => handleMapChange(index, 'description', e.target.value)}
                  as="p"
                  multiline
                  rows={4}
                  className="card-text"
                />
              </Card.Body>
              {isEditing && (
                <Card.Footer>
                  <Button variant="danger" className="w-100" onClick={() => handleRemoveMap(index)}>
                    Quitar Mapa
                  </Button>
                </Card.Footer>
              )}
            </Card>
          </Col>
        )) : <p>No hay mapas definidos para esta regin.</p>}
        {isEditing && maps.length === 0 && <p class="mt-2">Aade el primer mapa!</p>}
      </Row>
    </div>
  );
};

  


const ActivityView = ({ data, selectedEvent, onTimelineClick, isEditing, onDataChange }) => {
    // --- Declarar charts al inicio para evitar ReferenceError ---
    const charts = Array.isArray(data?.charts) && data.charts.length > 0
      ? data.charts.filter(c => c && c.chartData && Array.isArray(c.chartData.labels) && Array.isArray(c.chartData.datasets))
      : (data?.chartData ? [{
          chartData: data.chartData,
          analysis: data.analysis,
          name: 'Grfica principal'
        }] : []);

    // --- Mover todos los hooks despus de declarar charts ---
    const [selectedChartIndex, setSelectedChartIndex] = React.useState(0);

    // Asegurar que el ndice seleccionado siempre sea vlido
    React.useEffect(() => {
      if (charts && charts.length > 0 && (selectedChartIndex < 0 || selectedChartIndex >= charts.length)) {
        setSelectedChartIndex(0);
      }
    }, [charts, selectedChartIndex]);
    const [chartFlash, setChartFlash] = React.useState(false);

    // Validacin defensiva para evitar errores de acceso a undefined
    const safeChart = (charts && charts.length > 0 && charts[selectedChartIndex]) ? charts[selectedChartIndex] : (charts && charts.length > 0 ? charts[0] : null);

    React.useEffect(() => {
      setChartFlash(true);
      const timeout = setTimeout(() => setChartFlash(false), 600);
      return () => clearTimeout(timeout);
    }, [JSON.stringify(charts[selectedChartIndex]?.chartData)]);

    React.useEffect(() => {
      if (data && !Array.isArray(data.charts) && data.chartData) {
        onDataChange('charts', charts);
      }
    }, []);

    // Si no hay grfica vlida, no renderizar nada
    if (!safeChart || !safeChart.chartData || !Array.isArray(safeChart.chartData.labels) || !Array.isArray(safeChart.chartData.datasets)) {
      return null;
    }

    // Opciones de ejes duales para Produccin y Valor
    const getChartOptions = (chartData) => {
      let prodMax = 1, prodMin = 0, valorMax = 1, valorMin = 0;
      if (chartData && chartData.datasets) {
        if (chartData.datasets[0]) {
          chartData.datasets[0].data.forEach(val => {
            if (typeof val === 'number') {
              if (val > prodMax) prodMax = val;
              if (val < prodMin) prodMin = val;
            }
          });
        }
        if (chartData.datasets[1]) {
          chartData.datasets[1].data.forEach(val => {
            if (typeof val === 'number') {
              if (val > valorMax) valorMax = val;
              if (val < valorMin) valorMin = val;
            }
          });
        }
      }
      return {
        responsive: true,
        interaction: { mode: 'index', intersect: false },
        elements: { point: { radius: 5 } },
        plugins: { legend: { position: 'top' } },
        scales: {
          y: {
            type: 'linear',
            position: 'left',
            title: { display: true, text: 'Produccin' },
            beginAtZero: true,
            min: prodMin < 0 ? prodMin : 0,
            max: prodMax > 0 ? Math.ceil(prodMax * 1.1) : 1,
            ticks: {
              stepSize: Math.max(1, Math.ceil((prodMax - prodMin) / 5)),
              callback: function(value) { return value; }
            }
          },
          y1: {
            type: 'linear',
            position: 'right',
            title: { display: true, text: 'Valor' },
            beginAtZero: true,
            min: valorMin < 0 ? valorMin : 0,
            max: valorMax > 0 ? Math.ceil(valorMax * 1.1) : 1,
            grid: { drawOnChartArea: false },
            ticks: {
              stepSize: Math.max(1, Math.ceil((valorMax - valorMin) / 5)),
              callback: function(value) { return value; }
            }
          }
        }
      };
    };

    if (!data) return null;
    const handleTimelineClick = onTimelineClick || (() => {});

    const handleSelectChart = (idx) => setSelectedChartIndex(idx);
    const handleAddChart = () => {
      const newChart = {
        name: `Nueva grfica ${charts.length + 1}`,
        chartData: {
          labels: ["2025"],
          datasets: [{
            type: 'line',
            label: "Dato de Ejemplo",
            data: [100],
            borderColor: "rgba(54, 162, 235, 1)",
            yAxisID: 'y',
          }]
        },
        analysis: { behavior: '', growth: '' }
      };
      const newCharts = [...charts, newChart];
      onDataChange('charts', newCharts);
      setSelectedChartIndex(newCharts.length - 1);
    };
    const handleRemoveChart = (idx) => {
      if (charts.length === 1) return;
      if (window.confirm('Seguro que deseas eliminar esta grfica?')) {
        const newCharts = charts.filter((_, i) => i !== idx);
        onDataChange('charts', newCharts);
        setSelectedChartIndex(0);
      }
    };
    const handleChartNameChange = (idx, value) => {
      const newCharts = charts.map((c, i) => i === idx ? { ...c, name: value } : c);
      onDataChange('charts', newCharts);
    };
    const handleChartLabelChange = (index, value) => {
      const newCharts = [...charts];
      // Clonar chartData para forzar actualizacin
      const oldChartData = newCharts[selectedChartIndex].chartData;
      const newLabels = [...oldChartData.labels];
      newLabels[index] = value;
      newCharts[selectedChartIndex].chartData = {
        ...oldChartData,
        labels: newLabels,
        datasets: oldChartData.datasets.map(ds => ({ ...ds }))
      };
      onDataChange('charts', newCharts);
    };
    const handleChartDataChange = (datasetIndex, dataIndex, value) => {
      const newCharts = [...charts];
      // Clonar chartData para forzar actualizacin
      const oldChartData = newCharts[selectedChartIndex].chartData;
      const newDatasets = oldChartData.datasets.map((ds, i) =>
        i === datasetIndex
          ? { ...ds, data: ds.data.map((d, j) => (j === dataIndex ? parseFloat(value) || 0 : d)), yAxisID: i === 1 ? 'y1' : 'y' }
          : { ...ds, yAxisID: i === 1 ? 'y1' : 'y' }
      );
      newCharts[selectedChartIndex].chartData = {
        ...oldChartData,
        labels: [...oldChartData.labels],
        datasets: newDatasets
      };
      onDataChange('charts', newCharts);
    };
    const handleAnalysisChange = (field, value) => {
      const newCharts = [...charts];
      newCharts[selectedChartIndex].analysis[field] = value;
      onDataChange('charts', newCharts);
    };

    const handleRemoveYearByLabel = () => {
      const labels = charts[selectedChartIndex]?.chartData?.labels || [];
      if (!labels.length) {
        alert('No hay aos para eliminar.');
        return;
      }
      const labelToRemove = window.prompt('Ingrese la etiqueta (ao) a eliminar (ej: 2023):');
      if (labelToRemove === null) return; // cancelado
      const yearIdx = labels.indexOf(labelToRemove);
      if (yearIdx === -1) {
        alert('Etiqueta no encontrada.');
        return;
      }
      if (!window.confirm(`Eliminar la etiqueta "${labelToRemove}" y sus datos correspondientes?`)) return;
      
      const newCharts = charts.map((c, i) => {
        if (i === selectedChartIndex) {
          const newChartData = {
            ...c.chartData,
            labels: c.chartData.labels.filter((_, j) => j !== yearIdx),
            datasets: c.chartData.datasets.map(ds => ({
              ...ds,
              data: ds.data.filter((_, j) => j !== yearIdx)
            }))
          };
          return { ...c, chartData: newChartData };
        }
        return c;
      });
      onDataChange('charts', newCharts);
    };

    // --- Fin gestin de grficas ---

    const handleTimelineChange = (index, field, value) => {
      const newTimeline = [...data.timeline];
      newTimeline[index] = { ...newTimeline[index], [field]: value };
      onDataChange('timeline', newTimeline);
    };

    const handleAddTimelineEvent = () => {
      const newTimeline = [...(data.timeline || [])];
      newTimeline.push({ year: 'Nuevo Ao', event: 'Nuevo evento', detailedEvent: 'Detalles del nuevo evento.' });
      onDataChange('timeline', newTimeline);
    };
  
    const handleRemoveTimelineEvent = (index) => {
      const newTimeline = [...data.timeline];
      newTimeline.splice(index, 1);
      onDataChange('timeline', newTimeline);
    };

    return (
      <>
        <header className="text-center mb-5">
          <h1>
            <EditableField 
              isEditing={isEditing} 
              value={data.pageTitle} 
              onChange={(e) => onDataChange('pageTitle', e.target.value)} 
              as="span"
              style={{ fontSize: '2.5rem'}} 
            />
          </h1>
          <EditableField 
            isEditing={isEditing} 
            value={"Bienvenido al Anlisis Econmico de Colombia. Esta plataforma interactiva ofrece una visin detallada de los sectores econmicos del pas, desglosados por regiones y actividades productivas. Explore los datos para comprender mejor la estructura econmica, el impacto y el desarrollo de Colombia."} 
            onChange={(e) => {}}
            as="p"
            multiline
            className="lead"
          />
        </header>
  
        <Row className="mb-5">
          <Col md={12}>
            <Card className="chart-card" style={data.chartImage ? { backgroundImage: `url(${data.chartImage})` } : {}}>
              <Card.Header as="h5" style={{display:'flex',justifyContent:'space-between',alignItems:'center'}}>
                <span>Grficas y Anlisis</span>
                <Button variant="outline-primary" size="sm" onClick={() => {}} style={{minWidth:120}}>
                  Seleccionar Grfica
                </Button>
              </Card.Header>
              <Card.Body>
                {/* Selector de grficas */}
                <div className="mb-3 d-flex align-items-center gap-2">
                                    <DropdownButton
                    id="dropdown-chart-selector"
                    title={charts[selectedChartIndex]?.name || "Seleccionar Grfica"}
                    onSelect={e => handleSelectChart(Number(e))}
                    variant="secondary"
                  >
                    {charts.map((c, i) => (
                      <Dropdown.Item key={i} eventKey={i} active={selectedChartIndex === i}>
                        {c.name}
                      </Dropdown.Item>
                    ))}
                  </DropdownButton>
                  {isEditing && (
                    <>
                      <Button variant="success" size="sm" onClick={handleAddChart}>+ Agregar Grfica</Button>
                      {charts.length > 1 && (
                        <Button variant="danger" size="sm" onClick={() => handleRemoveChart(selectedChartIndex)}>- Eliminar</Button>
                      )}
                      <input
                        type="text"
                        className="form-control ms-2"
                        style={{width:180,display:'inline-block'}}
                        value={charts[selectedChartIndex].name}
                        onChange={e => handleChartNameChange(selectedChartIndex, e.target.value)}
                        placeholder="Nombre de la grfica"
                      />
                      <Button
                        variant="warning"
                        size="sm"
                        onClick={handleRemoveYearByLabel}
                        title="Quitar ao por etiqueta"
                        style={{marginLeft:8}}
                      >
                        Quitar Ao/Dato
                      </Button>
                    </>
                  )}
                </div>
                <Row>
                  <Col md={8}>
                    <div className={`chart-flash-anim ${chartFlash ? 'flash' : ''}`}
                      style={{transition:'box-shadow 0.3s', borderRadius: '12px', padding: '8px'}}>
                      {charts[selectedChartIndex] && charts[selectedChartIndex].chartData && charts[selectedChartIndex].chartData.labels && charts[selectedChartIndex].chartData.labels.length > 0 ? (
                        <Line data={{
                          ...charts[selectedChartIndex].chartData,
                          datasets: charts[selectedChartIndex].chartData.datasets.map((ds, i) => ({
                            ...ds,
                            yAxisID: i === 1 ? 'y1' : 'y'
                          }))
                        }} options={getChartOptions(charts[selectedChartIndex].chartData)} />
                      ) : (
                        <p>No hay datos de grfica disponibles.</p>
                      )}
                    </div>
                    <p className="text-muted text-center mt-2"><small>Nota: Los datos de precios son representaciones realistas del mercado y provienen directamente del DANE.</small></p>
                  </Col>
                  <Col md={4}>
                    <h5>Anlisis del Comportamiento</h5>
                    {charts[selectedChartIndex] && charts[selectedChartIndex].analysis && (
                      <>
                        <EditableField 
                          isEditing={isEditing} 
                          value={charts[selectedChartIndex].analysis.behavior} 
                          onChange={(e) => handleAnalysisChange('behavior', e.target.value)} 
                          as="p"
                          multiline
                          rows={5}
                        />
                        <h5 className="mt-3">Anlisis del Crecimiento</h5>
                        <EditableField 
                          isEditing={isEditing} 
                          value={charts[selectedChartIndex].analysis.growth} 
                          onChange={(e) => handleAnalysisChange('growth', e.target.value)} 
                          as="p"
                          multiline
                          rows={5}
                        />
                      </>
                    )}
                  </Col>
                </Row>
              </Card.Body>
            </Card>
          </Col>
        </Row>

  {isEditing && safeChart && safeChart.chartData && Array.isArray(safeChart.chartData.labels) && Array.isArray(safeChart.chartData.datasets) && safeChart.chartData.datasets.length > 0 && (
          <Row className="mb-5">
            <Col>
              <Card>
                <Card.Header as="h5">Editar Datos de la Grfica</Card.Header>
                <Card.Body>
                  <div className="table-responsive">
                    <table className="table table-bordered text-center">
                      <thead>
                        <tr>
                          <th>Etiqueta (Ao)</th>
                          {safeChart.chartData.datasets.map((dataset, dsIndex) => (
                            <th key={dsIndex}>
                              <div className="d-flex flex-column align-items-center justify-content-center gap-1">
                                <input
                                  type="text"
                                  className="form-control form-control-sm mb-1"
                                  style={{minWidth:80}}
                                  value={dataset.label}
                                  onChange={e => {
                                    const newCharts = [...charts];
                                    const oldChartData = newCharts[selectedChartIndex].chartData;
                                    const newDatasets = oldChartData.datasets.map((ds, i) => i === dsIndex ? { ...ds, label: e.target.value } : { ...ds });
                                    newCharts[selectedChartIndex].chartData = {
                                      ...oldChartData,
                                      labels: [...oldChartData.labels],
                                      datasets: newDatasets
                                    };
                                    onDataChange('charts', newCharts);
                                  }}
                                />
                                <select
                                  className="form-select form-select-sm mb-1"
                                  style={{minWidth:80}}
                                  value={dataset.type || 'line'}
                                  onChange={e => {
                                    const newCharts = [...charts];
                                    const oldChartData = newCharts[selectedChartIndex].chartData;
                                    const newDatasets = oldChartData.datasets.map((ds, i) => i === dsIndex ? { ...ds, type: e.target.value } : { ...ds });
                                    newCharts[selectedChartIndex].chartData = {
                                      ...oldChartData,
                                      labels: [...oldChartData.labels],
                                      datasets: newDatasets
                                    };
                                    onDataChange('charts', newCharts);
                                  }}
                                >
                                  <option value="line">Lineal</option>
                                  <option value="bar">Barras</option>
                                </select>
                                <input
                                  type="color"
                                  className="form-control form-control-color mb-1"
                                  title="Color de la lnea o barra"
                                  value={/^#([0-9A-Fa-f]{6})$/.test(dataset.borderColor || '') ? dataset.borderColor : '#1976d2'}
                                  onChange={e => {
                                    const color = e.target.value;
                                    const newCharts = [...charts];
                                    const oldChartData = newCharts[selectedChartIndex]?.chartData || { labels: [], datasets: [] };
                                    const newDatasets = oldChartData.datasets.map((ds, i) =>
                                      i === dsIndex
                                        ? {
                                            ...ds,
                                            borderColor: color,
                                            backgroundColor: ds.type === 'bar' ? color + '55' : (ds.backgroundColor || color + '33')
                                          }
                                        : { ...ds }
                                    );
                                    newCharts[selectedChartIndex].chartData = {
                                      ...oldChartData,
                                      labels: [...oldChartData.labels],
                                      datasets: newDatasets
                                    };
                                    onDataChange('charts', newCharts);
                                  }}
                                />
                                {charts[selectedChartIndex].chartData.datasets.length > 1 && (
                                  <Button size="sm" variant="danger" onClick={() => {
                                    const newCharts = [...charts];
                                    const oldChartData = newCharts[selectedChartIndex].chartData;
                                    const newDatasets = oldChartData.datasets.filter((_, i) => i !== dsIndex);
                                    newCharts[selectedChartIndex].chartData = {
                                      ...oldChartData,
                                      labels: [...oldChartData.labels],
                                      datasets: newDatasets
                                    };
                                    onDataChange('charts', newCharts);
                                  }}>-</Button>
                                )}
                              </div>
                            </th>
                          ))}
                          <th>
                            <Button size="sm" variant="success" onClick={() => {
                              const newCharts = [...charts];
                              const oldChartData = newCharts[selectedChartIndex].chartData;
                              const newDatasets = [
                                ...oldChartData.datasets,
                                {
                                  type: 'line',
                                  label: `Serie ${oldChartData.datasets.length + 1}`,
                                  data: oldChartData.labels.map(() => 0),
                                  borderColor: `hsl(${Math.floor(Math.random()*360)},70%,50%)`,
                                  backgroundColor: `hsla(${Math.floor(Math.random()*360)},70%,50%,0.3)`,
                                  yAxisID: 'y',
                                }
                              ];
                              newCharts[selectedChartIndex].chartData = {
                                ...oldChartData,
                                labels: [...oldChartData.labels],
                                datasets: newDatasets
                              };
                              onDataChange('charts', newCharts);
                            }}>+ Columna</Button>
                          </th>
                        </tr>
                      </thead>
                      <tbody>
                        {safeChart.chartData.labels.map((label, index) => (
                          <tr key={index}>
                            <td>
                              <input
                                type="text"
                                className="form-control"
                                value={label}
                                onChange={(e) => handleChartLabelChange(index, e.target.value)}
                              />
                            </td>
                            {safeChart.chartData.datasets.map((dataset, dsIndex) => (
                              <td key={dsIndex}>
                                <input
                                  type="number"
                                  className="form-control"
                                  value={dataset.data[index]}
                                  onChange={(e) => handleChartDataChange(dsIndex, index, e.target.value)}
                                />
                              </td>
                            ))}
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  </div>
                  <div className="d-flex justify-content-end mt-2">
                    <Button size="sm" variant="success" onClick={() => {
                      const newCharts = [...charts];
                      const chart = newCharts[selectedChartIndex];
                      // Aadir un nuevo ao y un dato por dataset
                      chart.chartData.labels.push('Nuevo Ao');
                      chart.chartData.datasets.forEach(ds => ds.data.push(0));
                      onDataChange('charts', newCharts);
                    }}>
                      + Aadir Ao/Dato
                    </Button>
                  </div>
                </Card.Body>
              </Card>
            </Col>
          </Row>
        )}
  
        <Row className="mb-5">
          <Col md={8}>
            <Card className="impact-card" style={data.impactImage ? { backgroundImage: `url(${data.impactImage})` } : {}}>
              <Card.Header as="h5">Anlisis de Impacto</Card.Header>
              <Card.Body>
                <h5><i className="fas fa-users me-2"></i>Impacto Social</h5>
                <EditableField 
                  isEditing={isEditing} 
                  value={data.impact.social} 
                  onChange={(e) => onDataChange('impact.social', e.target.value)} 
                  as="p"
                  multiline
                  rows={3}
                />
                <hr />
                <h5><i className="fas fa-landmark me-2"></i>Impacto Poltico</h5>
                <EditableField 
                  isEditing={isEditing} 
                  value={data.impact.political} 
                  onChange={(e) => onDataChange('impact.political', e.target.value)} 
                  as="p"
                  multiline
                  rows={3}
                />
                <hr />
                <h5><i className="fas fa-chart-line me-2"></i>Impacto Econmico</h5>
                <EditableField 
                  isEditing={isEditing} 
                  value={data.impact.economic} 
                  onChange={(e) => onDataChange('impact.economic', e.target.value)} 
                  as="p"
                  multiline
                  rows={3}
                />
              </Card.Body>
            </Card>
          </Col>
          <Col md={4}>
            <Card>
              <Card.Header as="h5">Empresa Representativa</Card.Header>
              <Card.Body className="text-center company-card-body" style={{ backgroundImage: `url(${data.representativeCompany.backgroundImage})` }}>
                {isEditing && (
                  <>
                    <label htmlFor="logo-upload" className="btn btn-sm btn-info mb-2">Cambiar Logo</label>
                    <input
                        id="logo-upload"
                        type="file"
                        accept="image/*"
                        style={{ display: 'none' }}
                        onChange={(e) => {
                            const file = e.target.files[0];
                            if (file) {
                                const reader = new FileReader();
                                reader.onloadend = () => {
                                    onDataChange('representativeCompany.logo', reader.result);
                                };
                                reader.readAsDataURL(file);
                            }
                        }}
                    />
                    <label htmlFor="bg-upload" className="btn btn-sm btn-info mb-2 ms-2">Cambiar Fondo</label>
                    <input
                        id="bg-upload"
                        type="file"
                        accept="image/*"
                        style={{ display: 'none' }}
                        onChange={(e) => {
                            const file = e.target.files[0];
                            if (file) {
                                const reader = new FileReader();
                                reader.onloadend = () => {
                                    onDataChange('representativeCompany.backgroundImage', reader.result);
                                };
                                reader.readAsDataURL(file);
                            }
                        }}
                    />
                  </>
                )}
                <Image src={data.representativeCompany.logo} alt={`Logo de ${data.representativeCompany.name}`} fluid className="company-logo mb-3" />
                <h5>
                  <EditableField 
                    isEditing={isEditing} 
                    value={data.representativeCompany.name} 
                    onChange={(e) => onDataChange('representativeCompany.name', e.target.value)} 
                    as="span"
                  />
                </h5>
                <CompanyInfoAccordion 
                  data={data.representativeCompany.description} 
                  isEditing={isEditing}
                  onChange={(newDescription) => onDataChange('representativeCompany.description', newDescription)}
                />
              </Card.Body>
            </Card>
          </Col>
        </Row>
  
        <Row>
          <Col>
            <Card>
              <Card.Header as="h5">Lnea de Tiempo de Eventos Clave</Card.Header>
              <Card.Body>
                <div className="timeline">
                  {data.timeline && data.timeline.length > 0 ? data.timeline.map((item, index) => (
                    <div key={index} className={`timeline-item ${selectedEvent?.year === item.year ? 'selected' : ''}`}>
                      <span className="circle" />
                      <div
                        className="timeline-item-content"
                        onClick={() => !isEditing && handleTimelineClick(item)}
                        style={item.image ? { backgroundImage: `url(${item.image})` } : {}}
                      >
                        <span className="tag">
                          <EditableField 
                            isEditing={isEditing} 
                            value={item.year} 
                            onChange={(e) => handleTimelineChange(index, 'year', e.target.value)} 
                            as="span"
                          />
                        </span>
                        <EditableField 
                          isEditing={isEditing} 
                          value={item.event} 
                          onChange={(e) => handleTimelineChange(index, 'event', e.target.value)} 
                          as="p"
                          multiline
                        />
                         {isEditing && (
                          <Button variant="danger" size="sm" className="mt-2" onClick={() => handleRemoveTimelineEvent(index)}>
                            Quitar
                          </Button>
                        )}
                      </div>
                      {selectedEvent?.year === item.year && !isEditing && (
                        <div className="timeline-event-details">
                          <h6>Detalles del Evento de {item.year}</h6>
                          <p>{item.detailedEvent}</p>
                        </div>
                      )}
                      {isEditing && (
                         <div className="timeline-event-details">
                          <h6>Detalles del Evento</h6>
                          <EditableField 
                            isEditing={isEditing} 
                            value={item.detailedEvent} 
                            onChange={(e) => handleTimelineChange(index, 'detailedEvent', e.target.value)} 
                            as="p"
                            multiline
                            rows={4}
                          />
                        </div>
                      )}
                    </div>
                  )) : <p>No hay eventos en la lnea de tiempo.</p>}
                  {isEditing && (
                    <div className="text-center mt-3">
                      <Button variant="success" onClick={handleAddTimelineEvent}>
                        Aadir Evento
                      </Button>
                    </div>
                  )}
                </div>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </>
    );
  };
  

// Component for "Mapas" view
const MapasView = ({ mapasData, selectedMap, showMapModal, onOpenModal, onCloseModal }) => (
    <div className="mapas-section">
      <header className="text-center mb-5">
        <h1>Mapas de Inters</h1>
        <p className="lead">Explora la distribucin geogrfica de los recursos y actividades econmicas.</p>
      </header>
      <Row className="g-4">
        {mapasData.map((mapItem) => (
          <Col md={6} lg={3} key={mapItem.id}>
            <Card className="h-100 shadow-sm">
              <Card.Img variant="top" src={mapItem.image} alt={mapItem.title} />
              <Card.Body className="d-flex flex-column">
                <Card.Title>{mapItem.title}</Card.Title>
                <Button variant="primary" className="mt-auto" onClick={() => onOpenModal(mapItem)}>
                  Ver Detalles
                </Button>
              </Card.Body>
            </Card>
          </Col>
        ))}
      </Row>
  
      <Modal show={showMapModal} onHide={onCloseModal} centered size="lg">
        <Modal.Header closeButton>
          <Modal.Title>{selectedMap?.title}</Modal.Title>
        </Modal.Header>
        <Modal.Body className="text-center">
          {selectedMap && <Image src={selectedMap.image} alt={selectedMap.title} fluid />}
          <p className="mt-3">{selectedMap?.description}</p>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={onCloseModal}>
            Cerrar
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );

export default App;