import React from 'react';

const EditableField = ({ isEditing, value, onChange, as: Component = 'p', multiline = false, ...props }) => {
  if (isEditing) {
    const editProps = {
      ...props,
      value: value,
      onChange: onChange,
      className: `form-control ${props.className || ''}`.trim()
    };

    if (multiline) {
      return <textarea {...editProps} />;
    } else {
      return <input type="text" {...editProps} />;
    }
  }

  return <Component {...props}>{value}</Component>;
};

export default EditableField;